"use client";

import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { collection, getDocs, addDoc } from "firebase/firestore";
import { db } from "@/lib/firebase";

interface CostCenter {
  id: string;
  codigo: string;
  nome: string;
  tipo: string;
  responsavel: string;
  orcamento: number;
  status: "ativo" | "inativo";
}

export default function CentroCustoPage() {
  const [costCenters, setCostCenters] = useState<CostCenter[]>([]);
  const [newCostCenter, setNewCostCenter] = useState({
    codigo: "",
    nome: "",
    tipo: "Departamento",
    responsavel: "",
    orcamento: 0,
  });
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  useEffect(() => {
    const fetchCostCenters = async () => {
      try {
        const costCentersSnap = await getDocs(collection(db, "centroCusto"));
        const costCentersData = costCentersSnap.docs.map(doc => ({
          id: doc.id,
          ...doc.data(),
        })) as CostCenter[];
        setCostCenters(costCentersData);
      } catch (error) {
        console.error("Error fetching cost centers:", error);
      }
    };

    fetchCostCenters();
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const docRef = await addDoc(collection(db, "centroCusto"), {
        ...newCostCenter,
        status: "ativo",
      });

      setCostCenters([...costCenters, {
        id: docRef.id,
        ...newCostCenter,
        status: "ativo",
      }]);

      setNewCostCenter({
        codigo: "",
        nome: "",
        tipo: "Departamento",
        responsavel: "",
        orcamento: 0,
      });
      setIsDialogOpen(false);
    } catch (error) {
      console.error("Error adding cost center:", error);
    }
  };

  const formatCurrency = (value: number) => {
    return new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL'
    }).format(value);
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold">Centros de Custo</h1>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button className="bg-black text-white hover:bg-gray-800">
              + Novo Centro de Custo
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Novo Centro de Custo</DialogTitle>
            </DialogHeader>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <Input
                  placeholder="Código"
                  value={newCostCenter.codigo}
                  onChange={(e) => setNewCostCenter({
                    ...newCostCenter,
                    codigo: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  placeholder="Nome"
                  value={newCostCenter.nome}
                  onChange={(e) => setNewCostCenter({
                    ...newCostCenter,
                    nome: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  placeholder="Responsável"
                  value={newCostCenter.responsavel}
                  onChange={(e) => setNewCostCenter({
                    ...newCostCenter,
                    responsavel: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  type="number"
                  placeholder="Orçamento Mensal"
                  value={newCostCenter.orcamento}
                  onChange={(e) => setNewCostCenter({
                    ...newCostCenter,
                    orcamento: parseFloat(e.target.value),
                  })}
                  required
                />
              </div>
              <Button type="submit" className="w-full bg-black text-white hover:bg-gray-800">
                Salvar
              </Button>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      <div className="grid gap-6">
        {costCenters.map((center) => (
          <div
            key={center.id}
            className="bg-white rounded-lg shadow p-6"
          >
            <div className="flex justify-between items-start">
              <div>
                <div className="flex items-center space-x-4 mb-2">
                  <h3 className="font-medium">{center.nome}</h3>
                  <span className="text-sm text-gray-500">Código: {center.codigo}</span>
                  <span className={`px-3 py-1 rounded-full text-sm ${
                    center.status === "ativo"
                      ? "bg-green-100 text-green-800"
                      : "bg-red-100 text-red-800"
                  }`}>
                    {center.tipo}
                  </span>
                </div>
                <p className="text-sm text-gray-500">
                  Responsável: {center.responsavel}
                </p>
                <p className="text-sm text-gray-500 mt-1">
                  Orçamento Mensal: {formatCurrency(center.orcamento)}
                </p>
              </div>

              <Button variant="ghost" size="sm">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="20"
                  height="20"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                >
                  <path d="M17 3a2.85 2.83 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3Z" />
                </svg>
              </Button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
